import backgroundVideo from './bg.mp4';
import backgroundVideoImg from './bg.jpg';

import './index.scss';

function Header(){
    return (
        <div className="header-wrapper">
            <div className="header">
                <video muted="muted" loop="loop" autoPlay="autoplay" poster={backgroundVideoImg}>
                    <source src={backgroundVideo} type="video/mp4" />
                </video>
                <div style={{margin: 'auto', zIndex: 20, display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
                    <div className="logo">
                        <span class="glitch" data-text="YAKIDAN TECHNOLOGIES">
                            YAKIDAN TECHNOLOGIES
                        </span>
                        {/* <span>YAKIDAN</span><span>TECHNOLOGIES</span> */}
                    </div>
                    <div className="tagName">
                        Компания <strong>software developer'a</strong> and <strong>ITMO University student'a</strong>
                    </div>
                </div>
                <div className="sub-tagname">
                    <span>Из башни Тони Старка с любовью</span>
                </div>
            </div>
            <div className="sub-header">
            <span>YAKIDANTECHNOLOGIESYAKIDANTECHNOLOGIESYAKIDANTECHNOLOGIESYAKIDANTECHNOLOGIESYAKIDANTECHNOLOGIESYAKIDANTECHNOLOGIESYAKIDANTECHNOLOGIESYAKIDANTECHNOLOGIESYAKIDANTECHNOLOGIESYAKIDANTECHNOLOGIES</span>
            </div>
        </div>
    );
}

export default Header;