import './index.scss';

import yakidanIcon from '../../imgs/icon_yakidan.jpg';
import vladIcon from '../../imgs/icon_vlad.png';
import serIcon from '../../imgs/icon_ser.png';
import loveuIcon from '../../imgs/icon_loveu.png';

function About(){
    return (
        <div className="about">
            <div className="container">
                <div className="title">
                    <div className="header-title">Почему YAKIDAN TECHNOLOGIES?</div>
                    <div className="header-title-sub"><strong>JetBrains</strong> пришёл хана</div>
                </div>
                
                <div className="block-wrapper" >
                        <div className="block-item-wrapper">
                            <div className="block-item">
                                <div className="block-item-quote">
                                    <div className="text">
                                        «После игнора от РЖД и отказа МТС от моих услуг, я решил создать свою компанию, без блекджека и Николая Федотенко, конкурент как никак. 
                                        Стоит отметить, что на момент открытия компании у меня знаний больше, чем у Линуса Торвальдса. Он проходил курс Маятина по компьютерной графике? Нет? Лол, а я да.»
                                    </div>
                                </div>
                                <div className="block-item-profile" style={{background: 'linear-gradient(0deg, rgba(142,68,173,1) 0%, rgba(155,89,182,1) 100%)'}}>
                                    <img src={yakidanIcon} alt="" />
                                    <div className="info">
                                        <div className="name">Якимов Даниил</div>
                                        <div className="status">Наш CEO</div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="block-item-wrapper">
                            <div className="block-item">
                                <div className="block-item-quote">
                                    <div className="text">
                                        «Собрались как-то шарпёр, джавист и линуксоид... А я им — пошли нафиг! Шарпёр, Джавист, останьтесь.»
                                    </div>
                                </div>
                                <div className="block-item-profile" style={{background: 'linear-gradient(180deg, rgba(52,152,219,1) 0%, rgba(41,128,185,1) 100%)'}}>
                                    <img src={loveuIcon} alt="" />
                                    <div className="info">
                                        <div className="name">ОЛЕГ</div>
                                        <div className="status">01eG</div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="block-item-wrapper">
                            <div className="block-item">
                                <div className="block-item-quote">
                                    <div className="text">
                                        «Я линуксоид, бегите!»
                                    </div>
                                </div>
                                <div className="block-item-profile"  style={{background: 'linear-gradient(180deg, rgba(241,196,15,1) 0%, rgba(243,156,18,1) 100%)'}}>
                                    <img src={vladIcon} alt="" />
                                    <div className="info">
                                        <div className="name">Юрченко Владислав</div>
                                        <div className="status">Установил Линукс и думает, что самый умный</div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="block-item-wrapper">
                            <div className="block-item">
                                <div className="block-item-quote">
                                    <div className="text">
                                        «Поспорил я как-то с перваком7, что к концу года я либо попаду в крутую IT-company, либо на бюджет. Как видите, в компанию я попал.»
                                    </div>
                                </div>
                                <div className="block-item-profile"  style={{background: 'linear-gradient(180deg, rgba(52,73,94,1) 0%, rgba(44,62,80,1) 100%)'}}>
                                    <img src={serIcon} alt="" />
                                    <div className="info">
                                        <div className="name">Папикян Сергей</div>
                                        <div className="status">Обожает фонк и бесплатное образование</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                </div>
            </div>
        </div>
    );
}

export default About;