import './index.scss';

import Kidan from '../../imgs/yakidan.png';
import Vlad from '../../imgs/vlad.png';
import Loveuloveme from '../../imgs/loveu.png';
import Ser from '../../imgs/ser.png';

function Team(){

    const _openProfile = profile => {
        window.open(`https://github.com/${profile}`, '_blank')
    };

    return (
        <div style={{width: '100%', display: 'flex', flexDirection: 'column', alignItems: 'center', flexWrap: 'wrap'}}>
            <div className="team container">
                <div className="codeline" style={{color: '#e74c3c'}}>yakidan["technologies"].about</div>
                <div style={{width: '100%'}}>
                    <div className="tagline big-title center">Компания, которая живёт на энтузиазме и вере в Якидана.</div>
                </div>

                <div className="text">Мы самая быстрорастущая компания на территории ИТМО, ещё семестр назад нас было двое, теперь нас четверо. Конечно, наша капитализация оставляет желать лучшего, но кому нужны деньги, когда у нас в компании пацаны из первой группы?</div>

                <div className="button" onClick={() => {window.open("https://vk.com/yakidan", "_blank")}}>Подать резюме</div>
            </div>

            <div className="team container">
                <div className="codeline" style={{color: '#3498db'}}>yakidan["technologies"].team</div>
                <div className="selector-title" >
                    <div className="header-title">Наша команда</div>
                    <div className="header-title-sub">Посмотрите на наших хлопчиков</div>
                </div>


                <div className="selector">
                    <div className="fake-selector">
                        <div className="inner" data-aos="fade" data-aos-offset="400" data-aos-delay="900">
                            <span>YAKIDAN
                            TECHNOLOGIES</span>
                            <span>since 2020</span>
                        </div>
                    </div>
                    <div className="selector-wrapper">
                        <div className="selector-item medium">
                            <div className="img-wrapper">
                                <img src={Vlad} alt="" data-aos="fade-left" data-aos-offset="400" data-aos-delay="1500"/>
                            </div>
                            <div className="selector-item__info-wrapper">
                                <div className="selector-item__info" data-aos="fade-down" data-aos-delay="0">
                                    <div className="name">Юрченко Владислав</div>
                                    <div className="contacts" style={{color: '#34495e'}}>
                                        <div className="tag" onClick={() => _openProfile('superpupervlad')}>@superpupervlad</div>|
                                        <div className="who">Стажёр</div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="selector-item">
                            <div className="img-wrapper">
                                <img src={Kidan} alt="" data-aos="fade-up" data-aos-offset="400" data-aos-delay="1000"/>
                            </div>
                            <div className="selector-item__info-wrapper">
                                <div className="selector-item__info" data-aos="fade-down" data-aos-delay="0">
                                    <div className="name">Якимов Даниил</div>
                                    <div className="contacts">
                                        <div className="tag" onClick={() => _openProfile('yakidan')}>@yakidan</div>|
                                        <div className="who">CEO</div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="selector-item medium">
                            <div className="img-wrapper">
                                <img src={Loveuloveme} alt="" data-aos="fade-right" data-aos-offset="400" data-aos-delay="1500"/>
                            </div>
                            <div className="selector-item__info-wrapper">
                                <div className="selector-item__info" data-aos="fade-down" data-aos-delay="0">
                                    <div className="name">ОЛЕГ</div>
                                    <div className="contacts" style={{color: '#3498db'}}>
                                        <div className="tag" onClick={() => _openProfile('loveuloveme')}>@loveuloveme</div>|
                                        <div className="who">Олег</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="selector-item small">
                            <div className="img-wrapper">
                                <img src={Ser} alt="" data-aos="fade-right" data-aos-offset="400" data-aos-delay="2000"/>
                            </div>
                            <div className="selector-item__info-wrapper">
                                <div className="selector-item__info" data-aos="fade-down" data-aos-delay="0">
                                    <div className="name">Сергей Папикян</div>
                                    <div className="contacts" style={{color: '#e74c3c'}}>
                                        <div className="tag" onClick={() => _openProfile('I-SER-I')}>@I-SER-I</div>|
                                        <div className="who">Стажёр</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Team;